
import { defineComponent } from 'vue'
import QuizList from '@/assets/QuizList.json'

interface QuizType {
  id: number,
  question: string,
  answer: string,
  view: {
    number1: string,
    number2: string,
    number3: string,
    number4: string
  },
  type: string
}

export default defineComponent({
  name: "QuizView",
  data() {
    return {
      dataList: QuizList,
      current: 0,
      hint: 2,
      hintUse: false,
      userSelect: [] as string[],
      userName: this.$route.query.userName,
      selectRandom: this.$route.query.selectRandom,
      selectType: this.$route.query.selectType,
      selectCount: this.$route.query.selectCount,
      MaxCount: 0,
      // Type Assertion - 변수명 as 변경할 타입 - vue에서는 값 as 변경할 타입

      /*
      as 키워드는 유니온 타입 같은 복잡한 타입을 하나의 정확한 타입으로 줄일 때 사용
      단, 하나의 타입일 때 사용시 에러 발생
      as 타입은 타입실드 임시해제용
      1. 왜 타입 에러가 나는지 모를 때 임시로 해결 하기 위해 사용하거나
      2. 내가 어떤 타입의 데이터가 들어올 지 확실히 알고 있을 때 에러를 방지하기 위해 사용
      */
    }
  },
  methods: {
    SelectValue(e : unknown){
      this.userSelect.push((e as string)[1]);
      console.log(this.userSelect)
    },
    // hintShow() {
    //   this.hint--;
    //   this.isShow = true;
    //   if( this.hint < 0 ){
    //     this.isShow = false;
    //     this.hint = 0;
    //   }
    // }
    useHint() :void {
      if(this.hintUse){return}
      if(this.hint < 1){return}
      this.hint--;
      this.hintUse ||= true;
      // 평가논리 계산법 - 값이 false 일 때 true로만 변경 가능 이외에 값은 변경 되지 않는다. 만약 여러가지 조건문이 필요하다면 if 문을 사용 권장
    },
    questionCount(){
      this.MaxCount = this.dataList.QuizList.filter((e) => {
        if(this.selectType !== '전체'){
          return e.type === this.selectType
        }else{
          return e.type
        }
      }).length // 내가 선택한 데이터의 개수
    }
  },
  computed: {
    progress() :number{
      return Math.floor((this.current / Number(this.selectCount)) * 100); // 백분율 계산 함수
      // progress 라는 이름의 타입을 넘버로 설정
      // 해당 값을 리턴하는 값은 나머지를 계산하지 않고 버림
      // 그 계산값은 현재의 문제 번호 / 문제의 개수 그 값을 나눈 다음 * 100을 곱한다.
      // 0 / 5 * 100 = 0
      // 1 / 5 * 100 = 20
      // 2 / 5 * 100 = 40
      // 3 / 5 * 100 = 60
      // 4 / 5 * 100 = 80
      // 5 / 5 * 100 = 100

      // floor - 소수점 나머지를 내림
      // ceil - 소수점 올림
      // round - 소수점 반올림
      /*
        const num = 3.141592;
        const ceil - Math.ceil(num) - 4 (뒤에 소수점 몇이던 관심없이 무조건 +1)
        const floor - Math.floor(num) - 3 (뒤에 소수점 몇이던 관심없이 그냥 없앰)
        const round - Math.round(num) - 3 (만약에 3.54592 라면 4가 출력)
      */
    },
    resultScore() : number{
      return this.selectQuestion.filter((e, index)=>{
          return e.answer === this.userSelect[index]
      }).length
    },
    randomView() :Array<Array<[string, unknown]>>{
      return this.selectQuestion.map((e, index) => {
        return Object.entries(this.selectQuestion[index].view).sort(() => Math.random() - 0.5)
      })
    },
    selectQuestion() : QuizType []{
      return this.dataList.QuizList.filter((e) => {
        if(this.selectType !== '전체'){
          return e.type === this.selectType
        }else{
          return e.type
        }
      })
    }
  },
  created(){
    if(Number(this.selectRandom) != 0){
      this.selectRandom = "1";
    }
    // if(this.selectRandom == "1"){
      this.dataList.QuizList.sort(() => Math.random() - 0.5)
    // }
    this.questionCount() //  question 개수를 호출

    if(Number(this.selectCount) > this.MaxCount){ // selectCount는 문자이므로 숫자로 바꿔주기 위해 Number() 를 써서 숫자 타입으로 바꿔준다.
      this.selectCount = this.MaxCount.toString();
    }

  }
})
